import React, { useState,  } from "react";
import style from "./pages.module.css";
 
import Itemview from "./itemview";
import Volume from "../assets/volum";
import parse from "html-react-parser";
 
const Page = (props) => {
   
  var [mohtava, mohtavagir] = useState("");
  var [seda, audiogir] = useState(null);
  var [firstword, wordgir] = useState(null);
  var [newsentence, sentencegir] = useState(null);
  var [maani,maanigir]=useState(null)
  var [tedad,tedadgir]=useState(50)
  var[sortbylessons,sortbygir]=useState(false)
  const loadmore=()=>{tedadgir(tedad+50)}
  const clickkol = (i) => {
    mohtavagir(i.description);
    
    audiogir('/media/'+i.audiofile);
  
    maanigir(i.maani)
    const kalamearray = i.kalame.split(" ");
    wordgir(kalamearray[0]);
    kalamearray.shift();
    const jomlejadid = kalamearray.join(" ");
    sentencegir(jomlejadid);
  };
  const playsound = () => {
   
    const audio = new Audio(seda);
    audio.play();
  };

  const htmlsaz = (str = "") => {
    const a = parse(str);
    return a;
  };
 
let lessonsarrays = [];
let lessons = props.level.slice(0, tedad)
 
  if(sortbylessons === true){
    
    if (props.level.length > 0) {
      for (let index = 1; index <= 42; index++) {
        const lesson = lessons.filter((p) => p.lesson === index);
        
        lessonsarrays = [...lessonsarrays, lesson];
      }
    
    }
  }else{
    lessons = lessons.sort(function(a, b){
      let x = a.number;
      let y = b.number;
      if (x > y) {return -1;}
      if (x < y) {return 1;}
      return 0;
    });
    lessonsarrays = [...lessonsarrays, lessons];
     
  }
  return (
    <>
      {props.message}
      <div className={style.savechanges}>
        <div
          style={{ color: "white", display: "inline-block" }}
        ><h2 style={{margin:0}}>{props.appname}</h2>{`Level${props.pagenum}`}</div>
        <button className={style.buttoni} onClick={()=>sortbygir(!sortbylessons)}>{sortbylessons===true?'changes':'lessons'}</button>
        <button onClick={props.savetchanges} className={style.savebutton}>
          save changes
        </button>
      </div>
      <div className={style.pagekol}> 
        <div className={style.textholder}>
          <div className={style.textkhli}>
            {firstword && (
              <>
                <p style={{ display: "inline-block" }}>
                  <strong>{firstword}</strong>
                  <Volume
                    onClick={() => playsound()}
                    className={style.button}
                  />
                  {newsentence}
                </p>
                <br />
                <p style={{ direction:'rtl'}} >{maani}</p>
              </>
            )}
            {htmlsaz(mohtava)}
          </div>
        </div>
        <div className={style.itemsholder}>
          {sortbylessons=== true &&  lessonsarrays && (
            <div className={style.item1}>
                {lessonsarrays.map((i, j) => (
                  <div key={j}>
                    <h3>{i[0] && `lesson ${i[0].lesson}`}</h3>
                    {i.map((a, k) => (
                      <Itemview
                        changelevel={props.changelevel}
                        iteem={a}
                        key={k}
                        clickkol={() => clickkol(a)}
                      />
                    ))}
                  </div>
                ))}
 {props.level.length > tedad && <button className={style.loadmore} onClick={()=>loadmore()}>load more</button>}
            </div>
          )}
          {sortbylessons=== false &&  lessonsarrays && (
            <div className={style.item1}> 
                {lessonsarrays[0].map((i, j) => (
                  <div key={j}> 
                      <Itemview
                        changelevel={props.changelevel}
                        iteem={i}
                        key={j}
                        clickkol={() => clickkol(i)}
                      />
                  </div>
                ))}
           {props.level.length > tedad && <button className={style.loadmore} onClick={()=>loadmore()}>load more</button>}
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default React.memo(Page) ;
