import { Route, Switch } from "react-router-dom";
import { useEffect, useState } from "react";
import style from "./leitnerhome.module.css";
import Page from "./page";
// import Kol from "./kol";
import { useDispatch, useSelector } from "react-redux";
import { getleitnerarray, savechange } from "./reduxaffairs/leitneractions";
 
const Leitnerhome = (props) => {
  const [arraykol, arraygir] = useState([]);
  // array vase emal be backend
  const [arraypost, arraysaz] = useState([]);
  const [message,messagegir]=useState()
  const[shomare,shomaregir]=useState(0)
  
  const dispatch = useDispatch();
  const l = useSelector((state) => state.larray);
  let { loading, data, error } = l;

  
  // inja vase taiide save shodane taghirate:
  const ll = useSelector((state) => state.leitnesavechangesrreducer);
  const { data:dataa}=ll
  if(dataa){
    setTimeout(() => {
      messagegir("")
    }, 1000) 
  }
  useEffect(() => {
    if(dataa){messagegir(dataa)}
  }, [ dataa]);
  // -------------------------------
  useEffect(() => {
    dispatch(getleitnerarray(props.userlogin,props.appname));    
  }, [dispatch,props.userlogin]);
  
  useEffect(() => {
    if (l.data) {
      arraygir(l.data);
      shomaregir(Math.max(...l.data.map(i=>i.number)))
      
    }    
  }, [l.data  ]);


  

  const savetchanges = () => {
    dispatch(savechange(props.userlogin, arraypost,props.appname));
  };

  const arraysaztopost = (e) => {
 
    const newobj = e;
 
    const arrofoldobjs = arraypost.filter((p) => p.id === e.id);
    if (arrofoldobjs.length !== 0) {
      const newarray = arraypost;
      const index = newarray.findIndex((p) => p.id === e.id);
      newarray[index] = newobj;
      arraysaz([...newarray]);
    } else {
      arraysaz([...arraypost, newobj]);
      
    }
  };

  let levels = null;
  let level1;
  let level2;
  let level3;
  let level4;
  let level5;
  let levelarrays;


  if (l.data) {
    levels = arraykol;
    level1 = levels.filter((p) => p.level === 1);
    level2 = levels.filter((p) => p.level === 2);
    level3 = levels.filter((p) => p.level === 3);
    level4 = levels.filter((p) => p.level === 4);
    level5 = levels.filter((p) => p.level === 5);
    levelarrays = [level1, level2, level3, level4, level5];
 
 
    
 
  }
  
  


  const changelevel = (id, updown) => {
    shomaregir(shomare+1)
    const objadid = levels.filter((p) => p.id === id);
 
    arraysaztopost({
      id: objadid[0].id,
      level: updown === true ? objadid[0].level + 1 : objadid[0].level - 1,
      number:  shomare+1
       
    });
    const newobj = {
      id: objadid[0].id,
      kalame: objadid[0].kalame,
      maani: objadid[0].maani,
      level: updown === true ? objadid[0].level + 1 : objadid[0].level - 1,
      description: objadid[0].description,
      audiofile: objadid[0].audiofile,
      lesson: objadid[0].lesson,
      number:  shomare+1
    };
    const listejadid = [...levels];
      
    const index = listejadid.findIndex((p) => p.id === newobj.id);
    listejadid[index] = newobj;
    arraygir(listejadid);
    levels = listejadid;
  }
 

  let javab=null
  if (loading) {javab='loading...'}
  if (error) {javab=error}
 if(props.userlogin.userinfo.haspaid===false){javab='مهلت استفاده به اتمام رسیده است  لطفا سرویس خودرا تمیدید کنید'} 

  return  <div className={style.kollli}>
        {javab &&  <div className={style.peigham}>
            <div className={style.peighamchild}>
              <h2 >{javab}</h2>
            </div>
          </div>}
 
          {data &&
          props.userlogin.userinfo.haspaid && (
            <main className={style.main}>
              <Switch>
                {/* <Route
                  exact
                  path={`/${props.appname}`}
                  render={() => (
                    <Kol changelevel={changelevel} levels={levelarrays} />
                  )}
                /> */}
                {levelarrays.map((i, j) => (
                  <Route
                    key={j}
                    exact
                    path={`/${props.appname}/page${j + 1}`}
                    render={() => (
                      <Page
                      appname={props.appname}
                        savetchanges={savetchanges}
                        changelevel={changelevel}
                        level={i}
                        pagenum={j + 1}
                        message={message}
                      />
                    )}
                  />
                ))}
              </Switch>
            </main>
          )
        }
      </div>
};

export default Leitnerhome;
