import {
  leitnerdatafail,
  leitnerdatarequest,
  leitnerdatasuccess,
  leitnersavechangedatafail,
  leitnersavechangedatarequest,
  leitnersavechangedatasuccess,
} from "./leitnerconstants";

export const leitnerreducer = (state = {}, action) => {
  switch (action.type) {
    case leitnerdatarequest:
      return { loading: true };
    case leitnerdatasuccess:
      return { loading: false, data: action.payload };
    case leitnerdatafail:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const leitnesavechangesrreducer = (state = {}, action) => {
  switch (action.type) {
    case leitnersavechangedatarequest:
      return { loading: true };
    case leitnersavechangedatasuccess:
      return { loading: false,success: true, data: action.payload };
    case leitnersavechangedatafail:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};
