import React, { useEffect  } from "react";
import { Route } from "react-router-dom";
import { Provider } from "react-redux";
import { useDispatch, useSelector } from "react-redux";
import store from "../appreciever/reduxaffairs/store";
import Leitnerhome from "../appreciever/leitnerhome";
const Lhome = (props) => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({ type: "arraygir", payload: headerdata });
    // bb()
  }, [dispatch]);
  const userlogin = useSelector((state) => state.userlogin);

  
  const headerdata = [
    {
      array: [
        { to: "/le1100/", name: "All" },
        { to: "/le1100/page1", name: "Level 1" },
        { to: "/le1100/page2", name: "Level 2" },
        { to: "/le1100/page3", name: "Level 3" },
        { to: "/le1100/page4", name: "Level 4" },
        { to: "/le1100/page5", name: "Level 5" },
      ],
    },
    { className: "leitner" },
  ];

  return (
    <Provider store={store}>
      <Route
        render={() => (
          <Leitnerhome appname='le1100' userlogin={userlogin} />
        )}
        path="/le1100"
      />
    </Provider>
  );
};

export default Lhome;