import {
  leitnerdatafail,
  leitnerdatarequest,
  leitnerdatasuccess,
  leitnersavechangedatafail,
  leitnersavechangedatarequest,
  leitnersavechangedatasuccess,
} from "./leitnerconstants";
import axios from "axios";
export const savechange = (userlogin,array,appname) => {
 
  return async (dispatch) => {
    try {
      dispatch({ type: leitnersavechangedatarequest });
      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userlogin.userinfo.token}`,
        },
      };
      const { data } = await axios.put(
        `/api/${appname}/userlevels/`,
        array,
        config
      );
    
      dispatch({ type: leitnersavechangedatasuccess, payload: data });      
    } catch (error) {
      dispatch({
        type: leitnersavechangedatafail,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
};

export const getleitnerarray = (userlogin,appname,) => {
  return async (dispatch) => {
    try {
      dispatch({ type: leitnerdatarequest });
      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userlogin.userinfo.token}`,
        },
      };
      const {data} = await axios.get(`/api/${appname}/userlevels/`, config);
      
      dispatch({ type: leitnerdatasuccess, payload: data });
    } catch (error) {    
      dispatch({
        type: leitnerdatafail,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
};
 
